<template>
  <v-row class="no-gutters">
    <v-col cols="max">
      <InputBlock/>
    </v-col>
    <v-col cols="5">
      <v-img style="padding: 30px; background: #000000; border: 10px solid #000000" src="../../assets/img5.jpg" :height="height"/>
    </v-col>
  </v-row>
</template>

<script>
import InputBlock from "../../components/auth/InputBlock";

export default {
  name: "Auth",
  components: {InputBlock},
  data: () => ({
    height: 0
  }),
  methods: {
    updateWidth() {
      this.height = window.innerHeight;
    },
  },
  created() {
    window.addEventListener('resize', this.updateWidth);
    this.updateWidth();
  },
}
</script>

