<template>
  <v-card width="70%" rounded="0" elevation="0" style="margin-top: 5%">
    <v-card color="#C2D3EF" width="190px" height="60px" elevation="0" rounded="0"
            style="margin-left: 10%; margin-top: 3%">
      <v-card-text>
        <p style="font-family: 'Century Gothic',serif; font-size: 35px; color: black; margin: 3% auto auto 4%;">
          Tips&Bills
        </p>
      </v-card-text>
    </v-card>
    <div style="margin-left: 10%">
      <p style="font-family: 'Century Gothic',serif; font-size: 35px; color: black; margin-left: 10%; margin-top: 10%">
        Добро пожаловать!
      </p>
      <p style="font-family: 'Century Gothic',serif; font-size: 25px; color: black; margin-left: 10%; margin-top: 1%">
        Вы входите в личный кабинет своей огранизации
      </p>
      <v-form style="margin-left: 15%;" v-model="valid">

        <p style="font-family: 'Century Gothic',serif; font-size: 24px; color: black; margin-top: 8%;">
          Ваше имя пользователя
        </p>
        <v-text-field
            :error-messages="loginErrors"
            @input="$v.login.$touch()"
            @blur="$v.login.$touch()"
            v-model="login"
            placeholder="      Имя пользователя"
        />
        <p style="font-family: 'Century Gothic',serif; font-size: 24px; color: black; margin-top: 5%;">
          Ваш пороль
        </p>
        <v-text-field
            :append-icon="eyeFlag ? 'mdi-eye' : 'mdi-eye-off'"
            :type="eyeFlag ? 'text' : 'password'"
            @click:append="eyeFlag = !eyeFlag"
            :error-messages="passwordErrors"
            @input="$v.password.$touch()"
            @blur="$v.password.$touch()"
            v-model="password"
            placeholder="      Пароль"
        />
      </v-form>
      <v-btn
          color="#C2D3EF"
          x-large
          elevation="0"
          width="60%"
          height="90px"
          @click="submit()"
          style="font-family: 'Century Gothic',serif; font-size: 30px; margin-left: 10%; color: black; margin-top: 3%; margin-bottom: 70px"
      >
        Войти в систему
      </v-btn>
      <v-alert v-if="errorFlag" type="error">
        {{ errorText }}
      </v-alert>
    </div>
    <div>
      <p style="font-family: 'Century Gothic',serif; font-size: 24px; color: black; margin-top: 6%; margin-left: 10%">
        Техническая поддержка
      </p>
      <p style="font-family: 'Century Gothic',serif; font-size: 24px; color: black; margin-left: 10%;line-height: 0.1;">
        +7 (9XX) XXX-XX-XX
      </p>
    </div>
  </v-card>
</template>

<script>
import {validationMixin} from 'vuelidate'
import {required} from 'vuelidate/lib/validators'
import axios from "axios";
import VueCookies from "vue-cookies";
import router from "../../router";

export default {
  mixins: [validationMixin],
  validations: {
    password: {required},
    login: {required},
  },
  name: "InputBlock",
  data: () => ({
    eyeFlag: false,
    password: '',
    login: '',
    valid: false,
    errorFlag: false,
    errorText: ''
  }),
  computed: {
    loginErrors() {
      const errors = []
      if (!this.$v.login.$dirty) return errors
      !this.$v.login.required && errors.push('Поле не может быть пустым')
      return errors
    },
    passwordErrors() {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.required && errors.push('Поле не может быть пустым')
      return errors
    },
  },
  methods: {
    submit() {
      this.$v.$touch()
      if (this.valid) {
        if (this.login !== '' && this.password !== '') {
          console.log(this.phone)
          let data = {
            username: this.login,
            password: this.password
          }
          axios.create({
            baseURL: 'https://tipsnbills.herokuapp.com/'
          }).post('/api/auth/signin', data)
              .then(resp => {
                console.log(resp.data.token)
                VueCookies.set('token', resp.data.token ? resp.data.token.toString() : '', "10h")
                this.errorFlag = false;
                router.push({path: '/admin/statistic'})
              }).catch(err => {
            this.errorFlag = true;
            this.errorText = err.response.data.message
          })
        }
      }
    },
  }
}
</script>

<style scoped>
body {
  position: fixed;
}

::-webkit-scrollbar {
  background: transparent;
}
</style>